'use strict';

// var SelectAddress = require('./components/selectAddress');
var CheckoutWelcome = require('./components/checkoutWelcome');
var DeliveryOptions = require('./components/deliveryOptions');
var AddressSelector = require('./components/addressSelector');
var DeliveryMethodSelector = require('./components/deliveryMethod');
var AddEditAddress = require('./components/addEditAddress');
var CheckoutProgress = require('./components/checkoutProgress');
var BillingAddress = require('./components/billingAddress');
var ChooseBillingAddress = require('./components/chooseBillingAddress');
var ClickCollect = require('./components/clickCollect');
var PurchaseSummary = require('./components/purchaseSummary');

$(document).ready(function() {

  // var selectAddress = new SelectAddress();
  var checkoutWelcome = CheckoutWelcome;
  var deliveryOptions = DeliveryOptions;
  var addressSelector = AddressSelector;
  var deliveryMethodSelector = DeliveryMethodSelector;
  var addEditAddress = AddEditAddress;
  var checkoutProgress = CheckoutProgress;
  var billingAddress = BillingAddress;
  var chooseBillingAddress = ChooseBillingAddress;
  var clickCollect = ClickCollect;
  var purchaseSummary = PurchaseSummary;
  var labelID;

  $('.customer-options--item label').on('keypress', function() {
    labelID = $(this).attr('for');
    $('#'+labelID).trigger('click');
  });

  checkoutWelcome.init();
  checkoutWelcome.$existingUser.on('click', function(e) {
    checkoutWelcome.changeExisting(e.target.value);
  });

  checkoutWelcome.$userFieldId.on('input propertychange keyup change', function(e) {
    var inputVal = this.value.length;
    checkoutWelcome.setBtnState(checkoutWelcome.$loginButton, inputVal);
  });

  checkoutWelcome.$guestFieldId.on('input propertychange keyup change', function() {
    var inputVal = this.value.length;
    checkoutWelcome.setBtnState(checkoutWelcome.$guestButton, inputVal);
  });

  checkoutWelcome.$guestForm.on('submit', function() {
    checkoutWelcome.setBtnState(checkoutWelcome.$guestButton, 0);
  });

  checkoutWelcome.$userForm.on('submit', function() {
    checkoutWelcome.setBtnState(checkoutWelcome.$loginButton, 0);
  });

  $(document).on('forgottenPasswordModal', function() {
    checkoutWelcome.attachModalHandler(checkoutWelcome);
  });

  deliveryOptions.init();
  deliveryOptions.$deliveryOptions.on('change', function() {
    if (deliveryOptions.checkChecked()) {
      deliveryOptions.changeAddress(true);
    } else {
      deliveryOptions.changeAddress(false);
    }
  });

  deliveryOptions.$deliveryContainer.on('submit', function() {
    deliveryOptions.setBtnState(deliveryOptions.$submitButton, 0);
  });

  deliveryOptions.$optionCTA.on('click keypress', function() {
    var current = $(this);
    var getInput = current.closest('label').attr('for');

    deliveryOptions.triggerFormSubmit('#click-collect-method', true, getInput);
  });

  addressSelector.init();
  addressSelector.$addressOptions.on('change', function() {
    if (addressSelector.checkChecked()) {
      addressSelector.changeAddress(true);
    } else {
      addressSelector.changeAddress(false);
    }
  });

  addressSelector.$optionCTA.on('click keypress', function() {
    var current = $(this);
    var getInput = current.closest('label').attr('for');

    addressSelector.triggerFormSubmit(getInput);
  });

  deliveryMethodSelector.init();
  deliveryMethodSelector.$methodOptions.on('change', function() {
    deliveryMethodSelector.changeMethod();
  });

  deliveryMethodSelector.$chooseDateSelect.on('change', function() {
    deliveryMethodSelector.changeMethod();
  });

  deliveryMethodSelector.$methodContainer.on('submit', function() {
    deliveryMethodSelector.setBtnState(deliveryMethodSelector.$submitButton, 0);
  });

  deliveryMethodSelector.$deliveryInfoText.on('input propertychange keyup change', function() {
    deliveryMethodSelector.deliveryCount();
  });

  addEditAddress.init();
  addEditAddress.$allInput.on('input propertychange change', function() {
    if (addEditAddress.checkValid(this)) {
      addEditAddress.changeState(addEditAddress.checkAllValid());
    }
  });

  addEditAddress.$addressContainer.on('submit', function() {
    addEditAddress.setBtnState(addEditAddress.$submitButton, 0);
  });

  checkoutProgress.init();

  billingAddress.init();
  billingAddress.$chosenType.on('input propertychange keyup change', function() {
    billingAddress.change();
  });

  billingAddress.$tandcCheck.on('change', function() {
    billingAddress.change();
  });

  billingAddress.$existingAddresses.on('change', function() {
    billingAddress.change();
  });

  billingAddress.$allText.on('change input propertychange keyup', function() {
    billingAddress.change();
  });

  billingAddress.$mobileNumber.on('input propertychange keyup change', function() {
    billingAddress.checkNumber();
  });

  billingAddress.$addressContainer.on('submit', function() {
    billingAddress.setBtnState(billingAddress.$continueButton, 0);
  });

  chooseBillingAddress.init();
  chooseBillingAddress.$selectBillingAddress.on('change', function() {
    chooseBillingAddress.change();
  });

  chooseBillingAddress.$tandcCheck.on('change', function() {
    chooseBillingAddress.change();
  });

  chooseBillingAddress.$addressContainer.on('submit', function() {
    chooseBillingAddress.setBtnState(chooseBillingAddress.$continueButton, 0);
  });

  $(document).on('clickCollectListLoaded', function() {
    clickCollect.attachCCHandler(clickCollect);
  });

  purchaseSummary.$nectarContainer.on('submit', function() {
    console.log(purchaseSummary.$submitButton.length);
    purchaseSummary.setBtnState(purchaseSummary.$submitButton, 0);
  });

  //IE Hack
  $('button[form]').click(function() {
    var formId = $(this).attr('form');
    $('#' + formId).submit();
  });

});
