var billingAddressID = '#tuPaymentDetailsForm';

var errorService = require('./errorService');

var newBillingAddress = (function() {
  return {
    $addressContainer: $(billingAddressID),
    $selectBillingAddress: $(billingAddressID + ' .selectBillingAddress'),
    $newAddressForm: $(billingAddressID + ' .new-address-form'),
    $selectAddressForm: $(billingAddressID + ' .existing-address-form'),
    $newAddressRequired: $(billingAddressID + ' .new-address-form .required'),
    $chosenType: $(billingAddressID + ' input[name="existingAddressChosen"]'),
    $existingAddresses: $(billingAddressID + ' select[name="existingAddressId"]'),
    $findAddressButton: $('.findAddressButton .addess-lookup'),
    $allText: $(billingAddressID + ' input[type="text"], select#newTitle'),
    $continueButton: $('#contPayment'),
    $tandcCheck: $('#termsAndConditionsId, #colleguesTermsAndConditionsId'),
    $allInput: $(billingAddressID).find('input[type=text], input[type=radio], input[type=checkbox]'),
    $existingAddress: false,
    $mobileNumber: $(billingAddressID + ' #newContactNumber'),

    _setExistingAddress: function(newVal) {
      if (typeof(newVal) === 'boolean') {
        this.$existingAddress = newVal;
      } else {
        return;
      }
    },

    _tandcAgree: function() {
      return $(this.$tandcCheck).prop('checked');
    },

    _toggleRequired: function(newState) {
      $.each(this.$newAddressRequired, function() {
        $(this).prop('required', newState);
      });

      $(this.$existingAddresses).prop('required', !newState);
    },

    _toggleButtonDisable: function(newValue) {
      $(this.$continueButton).prop('disabled', newValue);
    },

    canContinue: function() {
      // Agree to T&C first
      if (this._tandcAgree()) {
        // Is this existing Address?
        if (this.$existingAddress) {
          // Has an existing address been selected?
          if ($(this.$existingAddresses).val() !== '') {
            // All is good
            return true;
          } else {
            // Select an address mate!
            return false;
          }
        } else {
          // This is new address
          var x = true;
          $.each(this.$allText, function() {

            var el = $(this);
            if (!errorService.checkValid(this, true)) {
              x = false;
              errorService.throw(el.prop('id'), el);
            } else {
              errorService.clear(el);
            }

            if(el.is('.required') && el.val() === '') {
              x = false;
            }
          });

          return x;
        }
      } else {
        return false;
      }
    },

    checkChecked: function() {
      if (this.$chosenType.filter(':checked').prop('value') === 'true') {
        this._setExistingAddress(true);
        this.$existingAddresses.attr('required', true);
      } else {
        this._setExistingAddress(false);
        this.$existingAddresses.removeAttr('required');
      }
    },

    showHide: function() {
      if (this.$existingAddress === false) {
        this.$selectAddressForm.slideUp();
        this.$newAddressForm.slideDown();
        this._toggleRequired(true);
      }

      if (this.$existingAddress === true) {
        this.$selectAddressForm.slideDown();
        this.$newAddressForm.slideUp();
        this._toggleRequired(false);
      }
    },

    change: function() {
      this.checkChecked();
      this.showHide();
      this._toggleButtonDisable(!this.canContinue());
    },

    checkNumber: function() {
      var valid = errorService.checkValid(this.$mobileNumber, true);

      if (!valid) {
        errorService.throw($(this.$mobileNumber).attr('id'), this.$mobileNumber);
      } else {
        errorService.clear(this.$mobileNumber);
      }

    },

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    init: function() {
      this.change();
    }
  }
})();

module.exports = newBillingAddress;
