var nectarCardID = '#nectarCardForm';

var errorService = require('./errorService');

var purchaseSummary = (function() {
  return {
    $nectarContainer: $(nectarCardID),
    $allInput: $(nectarCardID + ' input[type=text]'),
    $submitButton: $('button[form=nectarCardForm]'),

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    checkValid: function(element, required) {

      var valid = errorService.checkValid(element, required);

      return valid;
    }
  }
})();

module.exports = purchaseSummary;
