var chooseBillingAddressID = '#choosePaymentCardsFormId';

var errorService = require('./errorService');

var chooseBillingAddress = (function() {
  return {
    $addressContainer: $(chooseBillingAddressID),
    $selectBillingAddress: $(chooseBillingAddressID + ' input[name=paymentCardId]'),
    $tandcCheck: $(chooseBillingAddressID + ' #termsAndConditionsId,' + chooseBillingAddressID + ' #colleguesTermsAndConditionsId'),
    $continueButton: $(chooseBillingAddressID + ' input[type=submit]'),
    $allInput: $(chooseBillingAddressID).find('input[type=radio], input[type=checkbox]'),
    $accept: false,

    _tandcAgree: function() {
      return $(this.$tandcCheck).prop('checked');
    },

    _toggleButtonDisable: function(newValue) {
      $(this.$continueButton).prop('disabled', newValue);
    },

    canContinue: function() {
      if (this._tandcAgree() && $(this.$selectBillingAddress).filter(':checked').val() !== '') {
        return true;
      } else {
        return false;
      }
    },

    change: function() {
      this._toggleButtonDisable(!this.canContinue());
    },

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    init: function() {
      this.change();
    }
  }
})();

module.exports = chooseBillingAddress;
