var deliveryOptionsFormID = '#delivery-options';
var clickCollectFormID = '#click-collect-method';
var combinedSelector = String(deliveryOptionsFormID + ',' + clickCollectFormID);

var deliveryOptions = (function() {
  return {

    $deliveryContainer: $(deliveryOptionsFormID),
    $ccContainer: $(clickCollectFormID),
    $submitButton: $(deliveryOptionsFormID + ' input[type=submit]'),
    $deliveryOptions: $(combinedSelector).find('input[type=radio]'),
    $optionLabels: $(combinedSelector).find('input[type=radio] + label'),
    $optionCTA: $(combinedSelector).find('input[type=radio] + label .customer-options--cta'),
    $allInput: $(combinedSelector).find('input[type=radio]'),
    $selectedAddress: false,

    _setAddress: function(newVal) {
      if (typeof(newVal) === 'boolean') {
        this.$selectedAddress = newVal;
      } else {
        return;
      }
    },

    _submitForm: function(selectedItem) {
      this.$ccContainer.submit();
      this.setBtnState(selectedItem, 0);
    },

    _toggleSubmit: function() {
      if (this.$selectedAddress) {
        this.$submitButton.prop('disabled', false);
      } else {
        this.$submitButton.prop('disabled', true);
      }
    },

    checkChecked: function() {
      if (this.$deliveryOptions.filter(':checked')[0]) {
        return true;
      } else {
        return false;
      }
    },

    getSelectedAddressStatus: function() {
      return this.$selectedAddress;
    },

    triggerFormSubmit: function(container, cta, selectItem) {
      var isCTA = cta || false;
      var isSelected = selectItem || null;
      var selectedItem = $('input[id="'+selectItem+'"]');
      if ($(selectedItem).is(":not(:disabled)") && isCTA) {
        selectedItem.attr('checked', true);
        this._submitForm(selectedItem);
      };
    },

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    // Check state of radio buttons and perform actions
    changeAddress: function(newVal) {
      if (typeof(newVal) === 'boolean' && newVal === true) {
        this._setAddress(true);
        this._toggleSubmit();
      } else {
        this._setAddress(false);
        this._toggleSubmit();
      }
    },

    init: function() {
      this.changeAddress(this.checkChecked());
    }
  }
})();

module.exports = deliveryOptions;
