var regexPatterns = (function() {
  return {
    'alphanumeric': /^[a-zA-Z0-9\s\-.,:/()]+$/,
    'alpha': /^[a-zA-Z\s\-.,:/()]+$/,
    'email': /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/gi,
    'postcode': /^[a-zA-Z]{1,2}\d{1,2}[a-zA-Z]?\s?\d[a-zA-Z]{2}$/i,
    'mobile': /^(\+44\s?7\d{3}|\(?07\d{3}\)?)\s?\d{3}\s?\d{3}$/,
    'nectar': /^(98263000)([0-9]{11})$/,
    'nectar-short': /^[0-9]{11}$/
  }
})();

module.exports = regexPatterns;
