var addEditAddressFormID = '#addEditAddress';

var errorService = require('./errorService');

var addEditAddress = (function() {
  return {
    $addressContainer: $(addEditAddressFormID),
    $submitButton: $(addEditAddressFormID + ' input[type=submit]'),
    $requiredInput: $(addEditAddressFormID + ' input[required=true], select'),
    $allInput: $(addEditAddressFormID + ' input[type=text], select[name=titleCode]'),
    $isValid: false,

    _setValid: function(newVal) {
      if (typeof(newVal) === 'boolean') {
        this.$isValid = newVal;
        this._toggleSubmit();
      } else {
        return;
      }
    },

    _toggleSubmit: function() {
      if (this.$isValid) {
        this.$submitButton.prop('disabled', false);
      } else {
        this.$submitButton.prop('disabled', true);
      }
    },

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    checkAllValid: function() {
      var v = true;
      var self = this;

      $.each(this.$allInput, function() {

        if (!self.checkValid(this, true)) {
          v = false;
        }

        var el = $(this);
        if(el.prop('required') === true && el.val() === '') {
          v = false;
        }
      });

      this._setValid(v);
      return v;
    },

    checkValid: function(element, required) {
      var valid = errorService.checkValid(element, required);
      if (!valid) {
        errorService.throw($(element).attr('id'), element);
      } else {
        errorService.clear(element);
      }

      this._setValid(valid);
      return valid;
    },

    getValid: function() {
      return this.$isValid;
    },

    changeState: function() {
      this.checkAllValid();
      this._toggleSubmit();
    },

    init: function() {
      if($(addEditAddressFormID).is('.isEditAddress')) {
        this.changeState(this.checkAllValid());
      } else {
        this._toggleSubmit();
      }
    }
  }
})();

module.exports = addEditAddress;
