// ----------------------------------------------------------------------
// Function for handling the ajax calls when selecting a delivery address
// ----------------------------------------------------------------------


var formID = '#secure-login';
var loginID = '#loginForm';
var guestLoginID = '#guestForm';
var welcomeCombinedSelector = String(loginID + ',' + guestLoginID);

var checkoutWelcome = (function() {
  return {

    // Paths should be only one
    $guestCheckoutUrl: '/login/checkout/guest',
    $userCheckoutUrl: '/checkout/j_spring_security_check',
    $guestFieldName: 'email',
    $userFieldName: 'j_username',
    $loginForms: $(welcomeCombinedSelector),
    $loginContainer: $(formID),
    $emailField: $(formID + ' input[type=email]'),
    $loginForm: $(formID + ' form'),
    $loginButton: $('#loginForm button[type=submit]'),
    $guestButton: $('#guestForm button[type=submit]'),
    $existingUser: $(formID + ' input[name=existing]'),
    $passwordField: $(formID + ' .password-field-container'),
    $userFieldId: $('#j_username'),
    $guestFieldId: $('#guest_email'),
    $existing: 0,
    $forgotModal: '#colorbox',
    $guestForm: $(guestLoginID),
    $userForm: $(loginID),
    $allGuestInput: $(guestLoginID).find('input[type=text], input[type=password]'),
    $allUserInput: $(loginID).find('input[type=text], input[type=password]'),

    _setExisting: function(newVal) {
      if (newVal === 1 || newVal === 0) {
        this.$existing = newVal;
      } else {
        return;
      }
    },

    _togglePassword: function() {
      if (this.$existing === 0) {
        this.$passwordField.slideUp();
      }
      if (this.$existing === 1) {
        this.$passwordField.slideDown();
      }
    },

    _toggleEmail: function() {
      if (this.$existing === 0) {
        this.$emailField.attr('name', this.$guestFieldName);
      }
      if (this.$existing === 1) {
        this.$emailField.attr('name', this.$userFieldName);
      }
    },

    _guestCheckout: function() {
      this.$loginForm.attr('action', this.$guestCheckoutUrl);
      this._toggleEmail();
      this._togglePassword();
    },

    _userCheckout: function() {
      this.$loginForm.attr('action', this.$userCheckoutUrl);
      this._toggleEmail();
      this._togglePassword();
    },

    getExisting: function() {
      return this.$existing;
    },

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    // Check state of radio buttons and perform actions
    changeExisting: function(newValue) {
      this._setExisting(parseInt(newValue));

      switch (this.$existing) {
        case 0:
          this._guestCheckout();
          break;
        case 1:
          this._userCheckout();
          break;
        default:
          break;
      }
    },

    attachModalHandler: function(context) {
      $(this.$forgotModal).find('input[type=text]').on('input propertychange keyup change', function() {
        var inputVal = $(context.$forgotModal).find('input[type=text]').val().length;
        context.setBtnState($(context.$forgotModal).find('button.tuButton'), inputVal);
      });
    },

    init: function() {
      this.changeExisting(0);

      if (this.$userFieldId.length > 0) {
        this.setBtnState(this.$loginButton, this.$userFieldId.val().length);
      }
    }
  }
})();

module.exports = checkoutWelcome;
