var ERRORS = require('./errors');
var regexPatterns = require('./regexPatterns');

var errorService = (function() {
  return {
    errorID: 'globalMessage',
    errorClass: 'has-error',
    errorContainer: '.ln-c-form-group',

    throw: function(message, selector) {
      var err = (ERRORS[message]) ? ERRORS[message] : "Unfortunately there was an error";

      var errorTemplate = '' +
        '<div id="' + this.errorID + '" style="display: none;">' +
        '<p class="ln-c-field-info ln-c-field-info--error ln-u-flush-bottom">' +
        err +
        '</p>' +
        '</div>';

      $(selector).closest(this.errorContainer).addClass(this.errorClass);
      if (!this.checkExistingError($(selector))) {
        $(errorTemplate).insertAfter($(selector)).slideDown();
      }
    },

    clear: function(selector) {
      var x = $(selector).next();
      if (x.is('#' + this.errorID)) {
        $(selector).closest(this.errorContainer).removeClass(this.errorClass);
        x.slideUp('normal', function() {
          $(this).remove();
        });
      }
    },

    checkExistingError: function(element) {
      if ($(element).next().is('#' + this.errorID)) {
        return true;
      } else {
        return false;
      }
    },

    checkValid: function(element, allowEmpty) {

      // default to true
      if (allowEmpty !== false) {
        allowEmpty = true;
      }

      var v = false;

      if (element) {
        var x = $(element).val();
        v = true;

        var check = $(element).attr('data-validate');

        if (x === '' && allowEmpty === false) {
          v = false;
        } else if (x !== '' && check && !regexPatterns[check].test(x)) {
          v = false;
        }

        return v;
      }

      return v;
    }
  }
})();

module.exports = errorService;
