var deliveryMethodFormID = '#deliveryMethodForm';

var deliveryMethodSelector = (function() {
  return {
    $methodContainer: $(deliveryMethodFormID),
    $submitButton: $(deliveryMethodFormID + ' input[type=submit]'),
    $chooseDate: $(deliveryMethodFormID + ' .customer-options--dropdown'),
    $chooseDateSelect: $(deliveryMethodFormID + ' .customer-options--dropdown select'),
    $methodOptions: $(deliveryMethodFormID + ' input[type=radio]'),
    $optionLabels: $(deliveryMethodFormID + ' input[type=radio] + label'),
    $selectedMethod: false,
    $deliveryInfoText: $(deliveryMethodFormID + ' #deliveryInfo'),
    $deliveryInfoCount: $(deliveryMethodFormID + ' #characterCount'),
    $allInput: $(deliveryMethodFormID).find($('input[type=radio], select, textarea')),
    $deliveryInfoLimit: 70,

    _setMethod: function(newVal) {
      if (typeof(newVal) === 'string') {
        this.$selectedMethod = newVal;
      }
    },

    _toggleSubmit: function() {
      if (this.$selectedMethod === 'standard-delivery') {
        this.$submitButton.prop('disabled', false);
      } else if (this.$selectedMethod === 'premium-delivery' && this.$chooseDateSelect.val() !== '') {
        this.$submitButton.prop('disabled', false);
      } else {
        this.$submitButton.prop('disabled', true);
      }
    },

    _toggleDate: function() {
      if (this.$selectedMethod === 'standard-delivery' || !this.$selectedMethod) {
        this.$chooseDateSelect.prop('required', false);
        this.$chooseDate.slideUp();
      } else if (this.$selectedMethod === 'premium-delivery') {
        this.$chooseDateSelect.prop('required', true);
        this.$chooseDate.slideDown();
      }
    },

    checkChecked: function() {
      var method = this.$methodOptions.filter(':checked').attr('data-deliveryMethod');
      if (typeof(method) === 'string') {
        this._setMethod(method);
        return true;
      } else {
        return false;
      }
    },

    getSelectedMethod: function() {
      return this.$selectedMethod;
    },

    // Check state of radio buttons and perform actions
    changeMethod: function(newVal) {
      this.checkChecked();
      this._toggleDate();
      this._toggleSubmit();
    },

    deliveryCount: function() {
      var infoLength = this.$deliveryInfoText.length ? this.$deliveryInfoText.val().replace(/\r(?!\n)|\n(?!\r)/g, "\r\n").length : 0;

      if (this.$deliveryInfoCount.length) {
        this.$deliveryInfoCount.text(this.$deliveryInfoLimit - infoLength);
      }
    },

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    init: function() {
      this.changeMethod();
      this.deliveryCount();
    }
  }
})();

module.exports = deliveryMethodSelector;
