var clickCollectFormID = '#selectStoreFormId';

var clickCollect = (function() {
  return {

    $clickCollectContainer: $(clickCollectFormID),
    $clickCollectResults: $('#storeFinder-result'),
    $clickCollectOptions: $(clickCollectFormID + ' input[type=radio]'),

    _submitForm: function(selectItem) {
      $(clickCollectFormID).submit();
      this.setBtnState(selectItem, 0);
    },

    checkChecked: function() {
      if (this.$clickCollectOptions.filter(':checked')[0]) {
        return true;
      } else {
        return false;
      }
    },

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    triggerFormSubmit: function(selectItem) {
      var selectedItem = $('input[id="'+selectItem+'"]');
      if(selectedItem.is(":not(:disabled)")){
        selectedItem.attr('checked', true);
        this._submitForm(selectedItem);
      }
    },

    attachCCHandler: function(context) {
      $(this.$clickCollectResults).find('input[type=radio] + label .customer-options--cta').on('click keypress', function(e) {
        var current = $(this);
        var getInput = current.closest('label').attr('for');
        clickCollect.triggerFormSubmit(getInput);
      });
    },

  }
})();

module.exports = clickCollect;
