var addressSelectorFormID = '.address-selector-form';

var addressSelector = (function() {
  return {
    $addressContainer: $(addressSelectorFormID),
    $submitButton: $(addressSelectorFormID + ' input[type=submit]'),
    $addressOptions: $(addressSelectorFormID + ' input[type=radio]'),
    $optionLabels: $(addressSelectorFormID + ' input[type=radio] + label'),
    $optionCTA: $(addressSelectorFormID + ' input[type=radio] + label .customer-options--cta'),
    $selectedAddress: false,

    _setAddress: function(newVal) {
      if (typeof(newVal) === 'boolean') {
        this.$selectedAddress = newVal;
      } else {
        return;
      }
    },

    _toggleSubmit: function() {
      if (this.$selectedAddress) {
        this.$submitButton.prop('disabled', false);
      } else {
        this.$submitButton.prop('disabled', true);
      }
    },

    _submitForm: function(selectItem) {
      $(addressSelectorFormID).submit();
      this.setBtnState(selectItem, 0);
    },

    checkChecked: function() {
      if (this.$addressOptions.filter(':checked')[0]) {
        return true;
      } else {
        return false;
      }
    },

    getSelectedAddressStatus: function() {
      return this.$selectedAddress;
    },

    triggerFormSubmit: function(selectItem) {
      var selectedItem = $('input[id="'+selectItem+'"]');
      if($(selectedItem).is(":not(:disabled)")){
        selectedItem.attr('checked', true);
        this._submitForm(selectedItem);
      }
    },

    setBtnState: function(button, val) {
      var setVal = val || 0;
      if (setVal === 0) {
        button.prop('disabled', true);
      } else {
        button.prop('disabled', false);
      }
    },

    // Check state of radio buttons and perform actions
    changeAddress: function(newVal) {
      if (typeof(newVal) === 'boolean' && newVal === true) {
        this._setAddress(true);
        this._toggleSubmit();
      } else {
        this._setAddress(false);
        this._toggleSubmit();
      }
    },

    init: function() {
      this.changeAddress(this.checkChecked());
    }
  }
})();

module.exports = addressSelector;
