var containerClass = '.checkout-progress-meter';

var checkoutProgress = (function() {
  return {
    $progressTrack: $(containerClass + ' .checkout-progress-track .checkout-progress'),
    $active: $(containerClass + ' .checkout-progress-point.active'),

    init: function() {
      if (this.$active.length === 0) {
        this.$progressTrack.css('width', '100%');
      } else {
        this.$progressTrack.css('width', this.$active.css('left'));
      }
    }
  }
})();

module.exports = checkoutProgress;
